
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType, getImageUrl } from "../../../js/utility.js"
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'

export default {
name: 'rangechoice',
components: {
Group,
Cell
},
data() {
return {
itemlist: [],
itemDetail: {},
selectlist: [],
allSelect: false,
allSelectList: [],
allSelectImage: require('../../../assets/un_selected.png')
}
},
watch: {
allSelect: function(val) {
if (val) {
this.allSelectImage = require('../../../assets/selected.png');
} else {
this.allSelectImage = require('../../../assets/un_selected.png');
}
},
},
computed: {
...mapState([
'userInfo',
'detailSecond',
])
},
mounted() {
this.getList('', this.userInfo.department_name);
},
methods: {
getList(department_id, department_name) {
AjaxHttp('POST', 'm=user&a=checkuser', { 'department_id': department_id }, this, function(res, wself, success) {
if (success) {
if (isArrayType(res.data.sub_department_list)) {
wself.handleDepartmentList(res.data.sub_department_list);
}
if (isArrayType(res.data.role_list)) {
wself.handleRoleList(res.data.role_list);
}

if (wself.itemlist.length > 0) {
var item = wself.allSelectList[wself.itemlist.length - 1];
item.allSelect = wself.allSelect;
}

wself.itemlist.push({
name: department_name,
id: department_id,
sub_department_list: isArrayType(res.data.sub_department_list) ? res.data.sub_department_list : [],
role_list: isArrayType(res.data.role_list) ? res.data.role_list : []
});
wself.allSelectList.push({ allSelect: false });



wself.allSelect = false;
wself.itemDetail = wself.itemlist[wself.itemlist.length - 1];
}
});
},
getUserImage(url) {
return getImageUrl(url);
},
currentSelect(index) {
if (this.itemlist.length === 1) {
return true;
} else if (this.itemlist.length - 1 === index) {
return true;
} else {
return false;
}
},
handleDepartmentList(lists) {
for (var j = 0; j < lists.length; j++) {
var item = lists[j];
var selected = false;
for (var i = 0; i < this.selectlist.length; i++) {
var bigitem = this.selectlist[i];
if (bigitem.selecttype === 'department') {
if (bigitem.department_id === item.department_id) {
selected = true;
}
}
}
item.selected = selected;
}
},
getSelectImage(selected) {
if (selected) {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
handleRoleList(lists) {
for (var j = 0; j < lists.length; j++) {
var item = lists[j];
var selected = false;
for (var i = 0; i < this.selectlist.length; i++) {
var bigitem = this.selectlist[i];
if (bigitem.selecttype === 'role') {
if (bigitem.role_id === item.role_id) {
selected = true;
}
}
}
item.selected = selected;
}
},
selectItem(type, item) {
var index = -1;
for (var i = 0; i < this.selectlist.length; i++) {
var bigitem = this.selectlist[i];
if (type === 'department' && bigitem.selecttype === 'department') {
if (bigitem.department_id === item.department_id) {
index = i;
}
} else if (type === 'role' && bigitem.selecttype === 'role') {
if (bigitem.role_id === item.role_id) {
index = i;
}
}
}
if (index === -1) { //选中的情况下 判断是否标记全选
item.selected = true;
item.selecttype = type;
item.selectindex = this.itemlist.length - 1;
this.selectlist.push(item);
} else {
item.selected = false;
this.selectlist.splice(index, 1);
}

this.allSelect = this.getSelectedAllSeleteMark();
},
getSelectedAllSeleteMark() { //获取全选后的标示
for (var i = 0; i < this.itemDetail.sub_department_list.length; i++) {
var item = this.itemDetail.sub_department_list[i];
if (!item.selected) {
return false;
}
}
for (var i = 0; i < this.itemDetail.role_list.length; i++) {
var item = this.itemDetail.role_list[i];
if (!item.selected) {
return false;
}
}
return true;
},
getSubordinate(item) { //获取下级
if (!item.selected) {
this.itemDetail = {};
this.getList(item.department_id, item.name);
}
},
navSelect(index) {
this.allSelectList.splice(index + 1, this.allSelectList.length - index - 1);
this.allSelect = this.allSelectList[index].allSelect;
console.log(this.allSelect);

this.itemlist.splice(index + 1, this.itemlist.length - index - 1);
this.itemDetail = this.itemlist[index];
},
handleAllSelect() { //全选按钮

var tempRemovelist = [];
for (var i = 0; i < this.selectlist.length; i++) {
var item = this.selectlist[i];
if (item.selectindex === this.itemlist.length - 1) {
tempRemovelist.push(item);
}
}

for (var i = 0; i < tempRemovelist.length; i++) {
var item = tempRemovelist[i];
for (var j = 0; j < this.selectlist.length; j++) {
var bigitem = this.selectlist[j];
if (bigitem === item) {
this.selectlist.splice(j, 1);
}
}
}



this.allSelect = !this.allSelect;
if (this.allSelect) {
for (var i = 0; i < this.itemDetail.sub_department_list.length; i++) {
var item = this.itemDetail.sub_department_list[i];
item.selecttype = 'department';
item.selected = true;
item.selectindex = this.itemlist.length - 1;
this.selectlist.push(item);
}

for (var i = 0; i < this.itemDetail.role_list.length; i++) {
var item = this.itemDetail.role_list[i];
item.selecttype = 'role';
item.selected = true;
item.selectindex = this.itemlist.length - 1;
this.selectlist.push(item);
}
} else {
for (var i = 0; i < this.itemDetail.sub_department_list.length; i++) {
var item = this.itemDetail.sub_department_list[i];
item.selecttype = 'department';
item.selectindex = this.itemlist.length - 1;
item.selected = false;
}

for (var i = 0; i < this.itemDetail.role_list.length; i++) {
var item = this.itemDetail.role_list[i];
item.selecttype = 'role';
item.selectindex = this.itemlist.length - 1;
item.selected = false;
}
}
},
deleteSelect(index) {
this.allSelect = false;
var bigitem = this.selectlist[index];
if (bigitem.selecttype === 'department') {
for (var i = 0; i < this.itemDetail.sub_department_list.length; i++) {
var item = this.itemDetail.sub_department_list[i];
if (bigitem.department_id === item.department_id) {
item.selected = false;
}
}
} else if (bigitem.selecttype === 'role') {
for (var i = 0; i < this.itemDetail.role_list.length; i++) {
var item = this.itemDetail.role_list[i];
if (bigitem.role_id === item.role_id) {
item.selected = false;
}
}
}
this.selectlist.splice(index, 1);
},
sure() {

if (this.selectlist.length === 0) {
this.detailSecond.hasSelect = false;
this.$store.commit('updateDetailSecond', this.detailSecond);
this.$vux.toast.text('请先选择', 'top');
return;
}

var departmentlist = [];
var rolelist = [];

var departmentCount = 0;
var departmentRoleCount = 0;
var roleCount = 0;
for (var i = 0; i < this.selectlist.length; i++) {
var bigitem = this.selectlist[i];
if (bigitem.selecttype === 'department') {
departmentlist.push(bigitem.department_id);
departmentCount = departmentCount + 1;
departmentRoleCount = departmentRoleCount + parseInt(bigitem.role_count);
} else if (bigitem.selecttype === 'role') {
rolelist.push(bigitem.role_id);
roleCount = roleCount + 1;
}
}




this.detailSecond.departmentlist = departmentlist;
this.detailSecond.rolelist = rolelist;
if (departmentCount > 0 && roleCount > 0) {
this.detailSecond.rangeName = (departmentRoleCount + roleCount) + '人' + '（含' + departmentCount + '个部门）';
} else if (departmentCount > 0 && roleCount === 0) {
this.detailSecond.rangeName = departmentCount + '个部门';
} else {
this.detailSecond.rangeName = (departmentRoleCount + roleCount) + '人';
}
this.detailSecond.refresh = true;
this.detailSecond.hasSelect = true;
this.$store.commit('updateDetailSecond', this.detailSecond);
this.$router.go(-1);
}
}
}




